.react-geo-propertygrid {
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 100%;

  .ant-spin-nested-loading {
    height: 100%;

    .ant-spin-container {
      height: 100%;
      display: flex;
      flex-direction: column;
    }
  }

  .ant-table {
    flex: 1;

    .ant-table-fixed-header .ant-table-header {
      margin-bottom: inherit !important; // Inline style from antd
      padding-bottom: inherit !important; // Inline style from antd
    }

    .ant-table-container {
      height: 100%;
      display: flex;
      flex-direction: column;

      .ant-table-header {
        overflow: inherit;
      }

      .ant-table-body {
        overflow: auto;
      }

      .ant-table-cell {
        padding: 4px;
      }

      th {
        text-align: center;
      }
    }
  }

  .ant-pagination {
    width: 100%;
    text-align: center;
  }
}
